.bracketMatchTeam {
    display: flex;
    border: 1px solid #C9C9BE;
    border-radius: 2px;
    height: 50%;
    width: 100%;
    box-sizing: border-box;
    color: #E0E0CD;
    vertical-align: middle;
}

.teamLogo {
    padding-left: 8px;
}

.bracketMatchTeamName {
    padding: 3px;
    width: 80%;
    word-wrap: break-word;
}

.bracketMatchTeamScore {
    background: #181818;
    width: 20%;
    min-width: 1.75em;
    text-align: center;
    border-left: 1px solid #C9C9BE;
    padding: 3px 0;
}

.bracketMatchTeamScoreScoreOnly {
    background: #181818;
    width: 20%;
    min-width: 1.75em;
    text-align: center;
    padding: 3px 0;
}

.team {
    background-color: #303030;
    border: none;
    color: #E0E0CD;
    overflow: hidden;
    font-weight: bolder;
}

.teamName {
    padding-left: 8px;
    vertical-align: top;
}

.fontWin {
    color: #C8FFC8;
}

.fontLose {
    font-weight: lighter;
    color: #FFD8D8;
}
