.matchDiv {
    position: relative;
    grid-row: span 4;
}

.unsetButton {
    all: unset;
    width: 100%;
    height: 100%;
}

.infoButton {
    position: absolute;
    top: calc(2em - 6px);
    right: 15%;
}

.gameInfo {
    position: absolute;
    top: calc(2em - 10px);
    left: 100%;
    height: 24px;
    display: flex;
    border: 1px solid #C9C9BE;
    border-left: 0px;
    background: #181818;
    z-index: 1;
}

.resetMatchGameInfo {
    position: absolute;
    float: left;
    top: -1px;
    left: 102%;
    padding-left: 3px;
    height: 24px;
    display: flex;
    border: 1px solid #C9C9BE;
    border-left: 0px;
    background: #181818;
    z-index: 1;
}

.smallTeamLogo {
    float: left;
}
